	/* MATERIAL PALETTE https://developer.microsoft.com/en-us/fluentui#/styles/web/colors/neutrals */

	/* COLORS */
	--blueGray900: #263238;
	--blueGray800: #37474f;
	--blueGray700: #455a64;
	--blueGray600: #546e7a;
	--blueGray500: #607d8b;
	--blueGray400: #78909c;
	--blueGray300: #90a4ae;
	--blueGray200: #b0bec5;
	--blueGray100: #cfd8dc;
	--blueGray50: #eceff1;

	--cyanA200: #18ffff;
	--cyanA400: #00e5ff;

	--deepOrangeA400: #ff3d00;

	/* FONT SIZE */
	--fontSize1: 3.052rem;
	--fontSize2: 2.441rem;
	--fontSize3: 1.953rem;
	--fontSize4: 1.563rem;
	--fontSize5: 1.25rem;
	--fontSize6: 1rem;
	--fontSize7: 0.8rem;

	/* FONT WEIGHT */
	--black: 800;
	--bold: 600;
	--medium: 400;
	--light: 300;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 18px; /* Root font size */
}

body {
	font-family: "Karla", sans-serif;
	color: white;
	background-color: #263238;
	background-color: var(--blueGray900);
}

.App {
	max-width: 1024px;
	margin: 24px auto;
}

a {
	text-decoration: none;
	color: white;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* TYPOGRAPHY */

h2 {
	font-size: 2.441rem;
	font-size: var(--fontSize2);
}

h3 {
	font-size: 1.953rem;
	font-size: var(--fontSize3);
}

.text {
	font-size: 1.25rem;
	font-size: var(--fontSize5);
	line-height: 150%;
}

/* --- */
.blueGray200 {
	color: #b0bec5;
	color: var(--blueGray200);
}

.cyanA400 {
	color: #00e5ff;
	color: var(--cyanA400);
}

